import React from "react";
import './styles/instructors.css';
import Papa from 'papaparse';
// import Calendly from './Calendly';


export default class Instructors extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: 0
    }
  }

  componentDidMount() {
    let file = new XMLHttpRequest();
    file.onload = function () {
      if (file.readyState === 4 && file.status === 200) {
        let [header, ...values] = Papa.parse(file.responseText).data;
        values = values.map(r => r.map(c => {
          const t = c.trim();
          return isNaN(t) ? t : Number(t);
        }));
        const arr = values.map(r => r.reduce((o, c, j) => Object.assign(o, {[header[j] && header[j].trim()]: c}), {}));
      

        for (let i = arr.length - 1; i > 0; i--) {
          const j = Math.floor(Math.random() * (i + 1));
          [arr[i], arr[j]] = [arr[j], arr[i]];
        }

        console.log(arr);
        this.setState({ data: arr });
      }

      else {
        console.log("status: " + file.status);
      }
    }.bind(this)

    const url = "https://docs.google.com/spreadsheets/d/e/2PACX-1vTRLJRhP2S66YW6NmliXeJ-7SNTkWQmOAXKXbonkrnSYxRNb_0qKQXhU5Cuvu6cNIqWUYemOcL_d-jv/pub?output=csv";
    file.open("GET", url, true);
    file.send();
  }

  render () {
    const intro = (
        <>
            <div className="container spacer"></div>
            <h2 className="d-flex justify-content-center">Meet our Instructors</h2>
            <br></br>
            <p className="container instructor-intro">
                Our team of instructors are Red Cross lifeguard, CPR, and first aid certified. 
                All of our instructors are required to complete in-water training and learn our curriculum.
                Get to know our team of instructors, and find one who will fit your needs!
            </p>
        </>
    )

    if (this.state.data === 0) {
      return (
          <>
            {intro}
            <div className="container">
                <p className="flex-d justify-content-center">loading...</p>
            </div>
          </>
        
      );
    }

    const list = this.state.data.map((instructor, key) => {
        const keys = Object.keys(instructor);
        const img_id = instructor[ keys[4] ].split("id=")[1]
        const img_url = `https://lh3.google.com/u/0/d/${img_id}`;

        return (
                
            <li className="row instructor" key={instructor[ keys[0] ]} >
                <div className="col-2">
                    <img className="align-middle" alt={`${instructor[ keys[1] ]} profile`} src={img_url}></img>
                </div>
                <div className="col-5">
                    <h6 className="name">{instructor[ keys[1] ]}</h6>
                    <p>Email: <a href={`mailto:${instructor[ keys[2] ]}`}>{instructor[ keys[2] ]}</a></p>
                    <p>Levels Taught: {instructor[ keys[3] ]}</p>
                    <p id="bio">{instructor[ keys[5] ]}</p>
                    <p>Schedule a lesson with me <a target="_blank" rel="noopener noreferrer" href={`https://${ instructor[ keys[6] ] }`}><u>HERE</u></a></p>
                    
                    {/* <div className="calendly-inline-widget" data-url="https://calendly.com/tylerlessons" style={{minWidth: '320px', height: '700px'}}></div>
                    <script type="text/javascript" src="https://assets.calendly.com/assets/external/widget.js" async></script> */}
                    
                </div>
                {/* <div className="col-5">
                    <Calendly styles={{height: '100px', width: '100px'}}/>
                </div> */}
                {/* <div className="col-5">
                    <Calendly link={`https://${ instructor[ keys[6] ] }`} styles={{height: '100px', width: '100px'}}/>
                </div> */}
            </li>
            
            )
        });

    

    return (
      <>
        {intro}
        <div className="container">
            <ul>{list}</ul>
        </div>
        
      </>
      
    );
  }
}


