import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import * as serviceWorker from "./serviceWorker";
import { BrowserRouter as Router, Route, Routes } from "react-router-dom";
import {
  Navigation,
  Footer,
  Home,
  About,
  Instructors,
  Blog,
  Posts,
  Post,
  Levels,
  FAQs
} from "./components";
import ScrollToTop from "./scrollToTop.js";

ReactDOM.render(
  <Router>
    <ScrollToTop />
    <Navigation />
    <Routes>
      <Route path="/Lesson-Site" element={<Home />} />
      <Route path="/Lesson-Site/about" element={<About />} />
      <Route path="/Lesson-Site/instructors" element={<Instructors />} />
      <Route path="/Lesson-Site/levels" element={<Levels />} />
      <Route path="/Lesson-Site/FAQs" element={<FAQs />} />
    </Routes>
    <Footer />
  </Router>,

  document.getElementById("root")
);

serviceWorker.unregister();
